<template>
  <div class="collapse multi-collapse show pl-3">
    <div :key="todo.id" class="todo-row-wrap p-2 my-1 d-flex align-items-center" :class="{ lock: todo.isCheck }">
      <div class="todo-row-card">
        <div class="form-check pl-3 mb-0">
          <font-awesome-icon class="ml-auto text-dark" size="lg" :icon="changeIconsByCheck(todo.isCheck)"
            @click="toggleCheck" />
          <router-link :to="{path: `/todoEdit/${todo.group}/${todo.key}`}" class="text-dark">
            <label class="form-check-label pl-2">
              {{todo.name}}
            </label>
          </router-link>
        </div>
      </div>
      <font-awesome-icon class="ml-auto" :class="{'text-primary':todo.isImportant}" size="lg"
        :icon="changeIconsByImportant(todo.isImportant)" @click="toggleImportant" />
      <font-awesome-icon class="ml-3" size="lg" icon="trash" @click="remove" />
    </div>
  </div>
</template>

<script>
import { rtdbDB, getUserDBRef } from '@/service/rtdb_db'

export default {
  props: {
    todo: {
      type: Object,
      required: true
    }
  },
  methods: {
    changeIconsByImportant (isImportant) {
      if (isImportant) {
        return ['fas', 'star']
      }
      return ['far', 'star']
    },
    changeIconsByCheck (isCheck) {
      if (isCheck) {
        return ['fas', 'check-square']
      }
      return ['far', 'square']
    },
    toggleCheck () {
      this.todo.isCheck = !this.todo.isCheck
      rtdbDB.ref(`${getUserDBRef()}/todoList/${this.todo.group}`).child(this.todo.key).set(this.todo)
    },
    toggleImportant () {
      this.todo.isImportant = !this.todo.isImportant
      rtdbDB.ref(`${getUserDBRef()}/todoList/${this.todo.group}`).child(this.todo.key).set(this.todo)
    },
    remove () {
      this.$swal.fire({
        title: '確定刪除',
        text: '刪除後將不可修復',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: '確定',
        cancelButtonText: '取消'
      }).then((result) => {
        if (result.value) {
          rtdbDB.ref(`${getUserDBRef()}/todoList/${this.todo.group}`).child(this.todo.key).remove()
          this.$swalSuccessHandle('刪除成功')
        }
      })
    }
  }
}
</script>

<style>

</style>



