<template>
  <div class="todolist-wrap">
    <half-circle-spinner v-if="!isLoading" :animation-duration="1000" :size="40" :color="'#007bff'" />
    <div v-if="isLoading && starredTodos">
      <template v-for="(group, key, i) in starredTodos">
        <label v-if="group" for="dateline" class="col-form-label" :key="i">
          <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="folder-open" />
          {{key}}
        </label>
        <TodoSingleRow v-for="childTodo in group" :todo='childTodo' :key="childTodo.key"  />
      </template>

      <!-- 沒寫就不會動 ？-->
      <p v-show="false">{{test}}</p>
    </div>
  </div>
</template>

<script>
import TodoSingleRow from '@/components/template/todo-single-row.vue'

import { rtdbDB, getUserDBRef } from '@/service/rtdb_db'

export default {
  components: {
    TodoSingleRow
  },
  data () {
    return {
      starredTodos: {},
      test: {}
    }
  },
  // mounted () {
  //   rtdbDB.ref(`${getUserDBRef()}/todoList`).orderByKey().on('child_added', snapshot => {
  //     var key = snapshot.key
  //     var data = {}

  //     rtdbDB.ref(`${getUserDBRef()}/todoList/${key}`).orderByChild('isImportant').equalTo(true).on('value', snapshotChild => {
  //       data = snapshotChild.val()
  //       if (data) {
  //         this.starredTodos[key] = Object.assign({}, data)
  //         this.test = data // bug，不知為何需增加一個變數塞資料，顯示在畫面，畫面其他資料才會動？
  //       } else {
  //         delete this.starredTodos[key]
  //       }
  //     })
  //   })

  //   setTimeout(() => {
  //     this.isLoading = true
  //   }, 4000)
  // },
  created () {
    rtdbDB.ref(`${getUserDBRef()}/todoList`).orderByKey().on('child_added', snapshot => {
      var key = snapshot.key
      var data = {}

      rtdbDB.ref(`${getUserDBRef()}/todoList/${key}`).orderByChild('isImportant').equalTo(true).on('value', snapshotChild => {
        data = snapshotChild.val()

        const init = () => new Promise(resolve => {
          const dict = {
            data,
            key
          }
          resolve(dict)
        })

        init()
          .then(({data, key}) => {
            if (data) {
              this.starredTodos[key] = Object.assign({}, data)
              this.test = data // bug，不知為何需增加一個變數塞資料，顯示在畫面，畫面其他資料才會動？
            } else {
              delete this.starredTodos[key]
            }
          })
          .finally(() => {
            this.isLoading = true
          })
      })
    })
  }
}
</script>



